import React, { useEffect } from "react";
import { useState } from "react";
import "./portfolio.scss";
import PortfolioList from "../portfolioList/portfolioList";
import { webPortfolio, AI, schoolPortfolio } from "../../data.js";
import showbinge from "../../showbinge.png";
import memories from "../../memories.png";
import cse160 from "../../971.jpg";
import Carousel from "react-elastic-carousel";
import researchpaper from "../../ResearchPaper.pdf";

export default function Portfolio() {
  const [selected, setSelected] = useState("course");
  const [data, setData] = useState([]);
  const [webData, setWebData] = useState([]);
  const [schoolData, setSchoolData] = useState([]);
  const [aiData, setAiData] = useState([]);

  const [courseData, setCourseData] = useState([]);
  const [personalData, setPersonalData] = useState([]);

  const [hover, setHover] = useState(false);
  const [text, setText] = useState("");

  const onHover = () => {
    setHover(true);
  };

  const onLeave = () => {
    setHover(false);
  };

  const list = [
    { id: "course", title: "Coursework" },
    { id: "personal", title: "Personal Projects" },
  ];

  const breakPoints = [
    { width: 1, itemsToShow: 1 },
    { width: 650, itemsToShow: 2, itemsToScroll: 2 },
    { width: 1000, itemsToShow: 3, itemsToScroll: 3 },
    { width: 1300, itemsToShow: 4, itemsToScroll: 4 },
  ];

  const array = [
    showbinge,
    "https://i.pinimg.com/originals/8e/e2/29/8ee229f5d3fbb39c11d271882327a45d.jpg",
    "https://onlinetyping.org/typing-lessons/images/touch-typing-keyboard.png",
    memories,
    cse160,
    "https://cdn3.vectorstock.com/i/1000x1000/98/97/audit-research-icon-flat-cartoon-paper-vector-23039897.jpg",
    "https://img.etimg.com/thumb/msid-83768811,width-640,resizemode-4,imgsize-48761/money-making-ideas.jpg",
    "https://www.simplilearn.com/ice9/free_resources_article_thumb/Graph%20Data%20Structure%20-%20Soni/what-is-graphs-in-data-structure.png",
    "https://www.sigarch.org/wp-content/uploads/2017/03/Welcome.jpg",
    "https://sloanreview.mit.edu/wp-content/uploads/2020/09/EG2-Redman-Davenport-Serious-Data-Science-1290x860-1.jpg",
    "https://econ2017.sites.olt.ubc.ca/files/2015/04/jpg_happiness.jpeg",
    "https://bs-uploads.toptal.io/blackfish-uploads/components/blog_post_page/content/cover_image_file/cover_image/1312209/retina_1708x683_cover-0323-MachineLearning_Dan_Newsletter-b21703f41d807514083e297955e1b46d.png",
    "https://www.perforce.com/sites/default/files/styles/social_preview_image/public/image/2020-01/image-blog-future-software-development.png?itok=eO9Fa9k-",
    "https://assets-global.website-files.com/5e9aa66fd3886aa2b4ec01ca/6507a9857dd32ba394160042_19.%20What%20is%20Software%20Engineering.png",
    "https://assets-global.website-files.com/5b6df8bb681f89c158b48f6b/5ce426d9a4a48f75aee6f062_what-is-a-computer-network.jpg",
    "https://studio.uxpincdn.com/studio/wp-content/uploads/2022/06/ui-design-vs-ui-development.png",
    "https://www.analyticssteps.com/backend/media/thumbnail/3033404/5694625_1665050003_Linux%20OSArtboard%201.jpg",
  ];

  useEffect(() => {
    switch (selected) {
      case "course":
        setCourseData(schoolPortfolio);
        break;
      case "personal":
        setPersonalData(webPortfolio);
        break;

      default:
        setCourseData(webPortfolio);
    }
  }, [selected]);

  const goToLink = (link) => {
    if (link === "researchpaper") {
      window.open(researchpaper);
    } else window.open(link);
  };

  return (
    <div className="portfolio" id="portfolio">
      <h1>Portfolio</h1>
      <ul>
        {list.map((item) => (
          <PortfolioList
            title={item.title}
            active={selected === item.id}
            setSelected={setSelected}
            id={item.id}
          />
        ))}
      </ul>
      {selected === "personal" && (
        <Carousel breakPoints={breakPoints} className="container">
          {personalData.map((d) => (
            <div className="item">
              <img
                src={array[d.img]}
                alt=""
                onClick={() => goToLink(d.link)}
                onMouseEnter={() => setText(d.text)}
                onMouseLeave={() => setText("")}
              />
              <h3>{d.title}</h3>
            </div>
          ))}
        </Carousel>
      )}
      {selected === "course" && (
        <Carousel breakPoints={breakPoints} className="container">
          {courseData.map((d) => (
            <div className="item">
              <img
                src={array[d.img]}
                alt=""
                onClick={() => goToLink(d.link)}
                onMouseEnter={() => setText(d.text)}
                onMouseLeave={() => setText("")}
              />
              <h3>{d.title}</h3>
            </div>
          ))}
        </Carousel>
      )}

      {/*
      <div className="container">
        {data.map((d) => (
          <div className="item">
            <img src={array[d.img]} alt="" onClick={() => goToLink(d.link)} />
            <h3>{d.title}</h3>
          </div>
        ))}
        </div>*/}
      <div className="description">
        <h3>{text}</h3>
      </div>
    </div>
  );
}
